
input[type=number] {
    -moz-appearance: textfield;
}
.active {

    color:cadetblue;
    text-align: center;
    font-size: 15px;
    border-radius: 4px;
    border: 2px solid cadetblue;
    background-color: #282c34;
    padding: 5px 10px;

}
.disabled{

    text-align: center;
    font-size: 15px;
    border-radius: 4px;
    border: 2px solid red;
    background-color: #282c34;
    padding: 5px 10px;
    color: #ff0000;

}
.block{
    display: flex;
}
